import React, { useState, useEffect } from "react";
import DataGrid from "../containers/DataGrid";
import UserContext from "../context/UserContext";
import { TextArea, Button, Box } from 'grommet'
import { updateConfig } from '../library/parse'
import { assign } from '../helpers/'
const Component = () => {
  const _user = React.useContext(UserContext);
  const [activeConfig, setActiveConfig] = useState({})

  useEffect(() => {
    setActiveConfig(_user && _user.config)
  }, [_user])
  const displayConfig = [
    { 
      name: 'Post Template' , 
      key: 'templateConfig', 
      description: 'This is the template for your post. Set it up so it will be auto filled everytime you post to your social media' 
    }]
  return (
    <>
      {
        _user &&  displayConfig.map((config, i) => {
            return <Box gap="small" direction="column">
            {config.name}
            {config.description}
            <TextArea 
                 rows="8"
                size="xlarge"
                value={activeConfig && activeConfig[config.key]}
                onChange={event => {

                    setActiveConfig({
                        ...activeConfig,
                        [config.key]: event.target.value
                    })
                }}
            />
            <Button 
                label="Update"
                primary
                onClick={() => {
                    updateConfig({
                        [config.key]: activeConfig[config.key]
                    }).then(() => assign('/dashboard'))
                }}
            />
            </Box>
        })
      }
    </>
  );
};

export default Component;
